import React, { memo } from 'react'
import {
  IconButton,
  Image,
  Drawer,
  DrawerBody,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  useDisclosure,
} from "@chakra-ui/react";

import Box from '../components/Box';
import Button from '../components/Button';
import Flex from '../components/Flex';
import { responsive } from '../components/ThemeProvider/theme';

import logo from './logo.svg'
import { ReactComponent as Menu } from './menu.svg'
import { inRange } from 'lodash';

const links = [
  {
    label: '首頁',
    link: 1
  },
  {
    label: '總體產值',
    link: 3
  },
  {
    label: '2020產值變化',
    link: 5
  },
  {
    label: '觀眾購票行為與人氣節目',
    link: 8
  },
  {
    label: '藝FUN券振興效果',
    link: 13
  },
  {
    label: '下載完整報告',
    link: 15
  },
]

const Header = ({ active, bodyHeight, getFullpageApi, currentPage, ...props }) => {
  const { isOpen, onOpen, onClose } = useDisclosure()
  return (
    <Flex
      position="fixed"
      top={0}
      left={0}
      right={0}
      alignItems="center"
      justifyContent={responsive('flex-end', 'space-between')}
      zIndex="docked"
      px="2em"
      _before={{
        content: '""',
        position: 'absolute',
        display: 'block',
        left: 0,
        right: 0,
        top: 0,
        bottom: 0,
        bgImage: 'linear-gradient(180deg, #000000 0%, #5C5C5C 100%)',
        opacity: Number(active),
        transition: 'opacity 0.75s',
      }}
      {...props}
    >
      <Box
        position={responsive('absolute', 'relative')}
        left={responsive('50%', 'auto')}
        transform={responsive(`translate(-50%, ${active ? '0' : `calc(${bodyHeight}px - 14em)`})`, 'none')}
        transition="all 1s"
        width={responsive(active ? '118px' : '138px', '138px')}
      >
        <Image src={logo}  />
      </Box>
      <IconButton
        variant="ghost"
        colorScheme="blackAlpha"
        icon={<Menu />}
        onClick={onOpen}
      />
      <Drawer
        isOpen={isOpen}
        placement="right"
        onClose={onClose}
      >
        <DrawerOverlay />
        <DrawerContent
          bg="linear-gradient(270deg, #242424 0%, #474747 53.13%, #5C5C5C 100%)"
          box-shadow="0px 5px 10px 4px rgba(255, 255, 255, 0.2)"
        >
          <DrawerCloseButton size="lg" fontSize="1.25em" color="white" _focus={{ outline: 'none' }} />
          <DrawerBody position="absolute" top="50%" transform="translateY(-50%)">
            {links.map(({ label, link }, i, { length }) => (
              <Box pt={i && responsive('1.5rem', '1.75rem')} key={i}>
                <Button.Transparent
                  border="none"
                  height="auto"
                  color={i === length - 1 ? currentPage + 1 === link && 'dance' : inRange(currentPage + 1, link, links[i + 1].link) && 'dance'}
                  onClick={() => {
                    onClose()
                    getFullpageApi().moveTo(link)
                  }}
                  _focus={{
                    outline: 'none',
                  }}
                  _active={{
                    bg: 'transparent'
                  }}
                >{label}</Button.Transparent>
              </Box>
            ))}
          </DrawerBody>
        </DrawerContent>
      </Drawer>
    </Flex>
  )
}

export default memo(Header)
